import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import store from "./store"
import mixin from "@/common/mixin/index.js"

import uView from 'uview-ui/index.js'
Vue.mixin(mixin)
Vue.use(uView)
uni.$u.config.unit = 'rpx'

global.show_toast = function(msg, icontype = 'success',duration=2000) {
	uni.showToast({
		title: msg,
		icon: icontype,
		duration: duration
	})
}


import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
	store,
    ...App
})
app.$mount()